export default {
	data() {
		return {
			tabIndex: 0,
			teamList: [],//保存正在拼团的商品
			bargainList:[],//保存正在砍价的商品
			openList:[],//保存马上开团的商品
			tabBars: [{
				name: '正在拼团'
			}, {
				name: '正在砍价'
			}, {
				name: '即将开团'
			}],

		}
	},
	created() {
		this.getTeamList();//正在拼团
	},
	onPageScroll(e) { //nvue暂不支持滚动监听，可用bindingx代替
		console.log(e);
		console.log("滚动距离为：" + e.scrollTop);
	},
	methods: {
		// 顶部导航
		ontabtap(e) {
			let index = e.target.dataset.current || e.currentTarget.dataset.current;
			if(this.tabIndex == index){
				return
			}else if(index == 0){
				this.getTeamList();//正在拼团
			}else if(index == 1){
				this.getBargainList();//正在砍价
			}else{
				this.getOpenList();//即将开团
			}
			this.tabIndex = index;
		},
		// 正在拼团
		getTeamList() {
			uni.request({
				url: this.$globalVariable.hostUrl + '/TeamActivity/index',
				method: 'POST',
				data: {
					page: 1,
					page_size: 20,
					type: 1
				},
				header: this.$globalVariable.requestHeader,
				success: res => {
					this.teamList = res.data.result.data;
				},
				fail: (err) => {
					alert(err);
				},
			});
		},
		// 正在砍价
		getBargainList(){
			uni.request({
				url: this.$globalVariable.hostUrl + '/ActivityBargain/index',
				method: 'POST',
				data: {
					page: 1,
					page_size: 20
				},
				header: this.$globalVariable.requestHeader,
				success: res => {
					this.bargainList = res.data.result.data;
				},
				fail: (err) => {
					alert(err);
				},
			});
		},
		// 马上开团
		getOpenList(){
			uni.request({
				url: this.$globalVariable.hostUrl + '/TeamActivity/index',
				method: 'POST',
				data: {
					page: 1,
					page_size: 20,
					type: 2
				},
				header: this.$globalVariable.requestHeader,
				success: res => {
					this.openList = res.data.result.data;
					console.log("res123:", res);
				},
				fail: (err) => {
					alert(err);
				},
			});
		}
	}
}
